@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.pexeso-box {
  width: 150px;
  height: 150px;
  margin: 10px;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 5px 6px 8px -2px #0a0909;
  border-radius: 10px;
}
.pexeso-box .theme-chinese {
  /* font-family: "KaiTi,SimSun,'Noto Sans SC'"; */
  font-family: KaiTi, SimSun, "Microsoft YaHei", "Arial Unicode MS",
    "Arial Unicode", serif;
}

.pexeso-box.hidden {
  opacity: 0;
  transition: opacity 0.3s;
  -webkit-transition: opacity 0.3s;
}
.pexeso-box:hover {
  box-shadow: none;
  position: relative;
  top: 1px;
  left: 1px;
  color: #ddd;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.question {
  font-size: 45px;
}
.flip-vertical-right {
  -webkit-animation: flip-vertical-right 0.4s
    cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
  animation: flip-vertical-right 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955)
    both;
}
.slide-fwd-center {
  -webkit-animation: slide-fwd-center 1.45s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    both;
  animation: slide-fwd-center 1.45s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
