#root {
  text-align: center;
}
.Pexeso {
  margin: 5px;
  display: inline-block;
}

.header {
  display: flex;
}
.header-icons {
  flex: 1 1;
}
.settings:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.header,
.footer {
  height: 40px;
  padding: 10px;
  background-color: #ddd;
  color: black;
  font-family: "Roboto";
  font-size: 30px;
  border: 1px solid;
  margin: 10px;
}
.footer {
  font-size: 15px;
  height: 20px;
}
.footer a:visited {
  color: #0000ee;
}
.box-list {
  display: flex;
  justify-content: space-evenly;
  align-content: space-between;
}
.box-row {
  margin-bottom: 15px;
}
.game-over {
  font-size: 35px;
  font-family: "Roboto";
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  display: inline-block;
  border: none;
  padding: 1rem 2rem;
  margin: 20px;
  text-decoration: none;
  background: #0069ed;
  color: #ffffff;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  -webkit-transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease, -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  border-radius: 5px;
  box-shadow: 5px 6px 8px -2px #0a0909;
}

button:hover,
button:focus {
  background: #0053ba;
}

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

button:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}

